@import url(https://fonts.googleapis.com/css2?family=Scheherazade+New:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Scheherazade+New:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.header{
    width: 100%;
    cursor: pointer;
    position: fixed;
    display: flex;
    justify-content: center;
    text-transform: capitalize;
    background-color: #2d3436;
    font-family: 'Scheherazade New', serif;
    font-size: 3.5vw;
    padding-bottom: 15px;
    box-shadow: 0px 1px 5px black;
    text-shadow: 0px 5px 15px black ;
    color: white;
    z-index: 100;


}
i{
    padding: 30px;
}

@media (max-width:1000px){
    .header{
        padding-top: 15px;
        font-size: 6.4vw;
    }
}
*{
  scroll-behavior: smooth;
}
.App{
  min-height: 100vh;
  background-color: #636e72;
  color: white;
  padding-top: 130px;
  padding-bottom: 70px;
}


@media (max-width:700px){
  .App{
    padding-top: 150px;
  }
}

.page-title {
  text-transform: uppercase;
  display: flex;
  justify-content: center;
  font-family: 'Scheherazade New', serif;
  font-size: 4vw;
  padding: 0px;
  color: white;
}

@media (max-width: 1000px) {
  .page-title {
    font-size: 6.4vw;
  }
}
.nav{
    
    width: 100%;
    position: fixed;
    bottom: 0;
    z-index: 100;
    
   
}
.media {
    display: flex;
    flex-direction: column;
    width: 200px;
    padding: 5px;
    margin: 5px 0;
    background-color: #282c34;
    border-radius: 10px;
    position: relative;
    font-family: 'Poppins', sans-serif;
  }
  
  .media:hover {
    background-color: white;
    color: black;
  }
  
  @media (max-width: 550px) {
    .media {
      width: 42%;
    }
  }
  
  .poster {
    border-radius: 10px;
  }
  
  .title {
    width: 100%;
    text-align: center;
    font-size: 17px;
    padding: 8px 0;
  }
  
  .subTitle {
    display: flex;
    justify-content: space-around;
    padding-bottom: 3px;
    padding: 0 2px;
    
  }


.ContentModal__landscape {
  object-fit: contain;
  border-radius: 10px;
  
}

.ContentModal__portrait {
  display: none;
  object-fit: contain;
  border-radius: 10px;
  height: 600px;
  padding-top: 100px;
  padding-left: 80px;
  
}

.tagline {
  padding-bottom: 10px;
  align-self: center;
}

.ContentModal {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
  width: 100%;
  overflow-y: scroll;
  scrollbar-width: none;
}

.ContentModal::-webkit-scrollbar {
  display: none;
}

.ContentModal__about {
  padding: 10px;
  width: 85%;
  height: 90%;
  display: flex;
  flex-direction: column;
  font-family: "Roboto", sans-serif;
  justify-content: space-evenly;
  font-weight: 300;
}

.ContentModal__title {
  height: 12%;
  font-size: 4vw;
  display: flex;
  align-items: center;
  justify-content: center;
}

.ContentModal__description {
  display: flex;
  height: 40%;
  overflow-y: scroll;
  padding: 15px;
  border-radius: 20px;
  /* scrollbar-width: none;  */
  box-shadow: inset 0 0 5px #000000;
  text-align: justify;
  margin: 25px;
}
/* .button{
  width: 100%;
  height: 50px;
} */

.ContentModal__description::-webkit-scrollbar {
  display: none;
}

@media (min-width: 835px) {
  .ContentModal__landscape {
    display: none;
  }
  .ContentModal__portrait {
    display: flex;
    width: 50%;
  }
  .ContentModal {
    flex-direction: row;
    justify-content: space-between;
    padding: 15px 0;
  }
  .ContentModal__about {
    width: 60%;
    padding: 70px;
    height: 100%;
  }
  .ContentModal__title {
    font-size: 3.4vw;
  }
  .ContentModal__description {
    font-size: 22px;

  }
}
.carouselItem {
    display: flex;
    flex-direction: column;
    object-fit: contain;
    padding: 10px;
    margin: 10px;
  }
  .carouselItem__img {
    border-radius: 10px;
    margin-bottom: 5px;
    box-shadow: 0px 0px 5px black;
  }

  @media (min-width: 835px){
    .carousel{
      margin: 40px;
    }
  }
.trending {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    grid-gap: 25px;
    gap: 25px;
  }
.pagination{
    background: transparent;
    
}
.tags{
    font-weight: bold;
    
}
