

.ContentModal__landscape {
  object-fit: contain;
  border-radius: 10px;
  
}

.ContentModal__portrait {
  display: none;
  object-fit: contain;
  border-radius: 10px;
  height: 600px;
  padding-top: 100px;
  padding-left: 80px;
  
}

.tagline {
  padding-bottom: 10px;
  align-self: center;
}

.ContentModal {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
  width: 100%;
  overflow-y: scroll;
  scrollbar-width: none;
}

.ContentModal::-webkit-scrollbar {
  display: none;
}

.ContentModal__about {
  padding: 10px;
  width: 85%;
  height: 90%;
  display: flex;
  flex-direction: column;
  font-family: "Roboto", sans-serif;
  justify-content: space-evenly;
  font-weight: 300;
}

.ContentModal__title {
  height: 12%;
  font-size: 4vw;
  display: flex;
  align-items: center;
  justify-content: center;
}

.ContentModal__description {
  display: flex;
  height: 40%;
  overflow-y: scroll;
  padding: 15px;
  border-radius: 20px;
  /* scrollbar-width: none;  */
  box-shadow: inset 0 0 5px #000000;
  text-align: justify;
  margin: 25px;
}
/* .button{
  width: 100%;
  height: 50px;
} */

.ContentModal__description::-webkit-scrollbar {
  display: none;
}

@media (min-width: 835px) {
  .ContentModal__landscape {
    display: none;
  }
  .ContentModal__portrait {
    display: flex;
    width: 50%;
  }
  .ContentModal {
    flex-direction: row;
    justify-content: space-between;
    padding: 15px 0;
  }
  .ContentModal__about {
    width: 60%;
    padding: 70px;
    height: 100%;
  }
  .ContentModal__title {
    font-size: 3.4vw;
  }
  .ContentModal__description {
    font-size: 22px;

  }
}